'use strict';

export default {
  //手机号码
  phone : /^^((13[0-9])|(15[^4])|(18[0235-9])|(17[0-8])|(147))\d{8}$$/,
  //邮箱
  email : /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/
};



